import React, { Component } from 'react'
import * as go from 'gojs'

const $ = go.GraphObject.make

const myModel = { "class": "go.GraphLinksModel",
  "nodeDataArray": [
    {"category":"Start", "name":"Start", "key":-1, "loc":"-380 -20"},
    {"name":"Select", "figure":"Diamond", "key":-2, "loc":"-280 -20"},
    {"name":"Transcoding", "key":-3, "loc":"-160 -70"},
    {"name":"Package", "key":-4, "loc":"-160 30"},
    {"name":"Encryption", "key":-5, "loc":"-40 -70"},
    {"name":"Uploader", "key":-8, "loc":"-40 30"},
    {"category":"End", "name":"End", "key":-10, "loc":"80 -20"}
  ],
  "linkDataArray": [
    {"from":-1, "to":-2, "points":[-360,-20,-350,-20,-340.25,-20,-340.25,-20,-330.5,-20,-320.5,-20]},
    {"from":-2, "to":-3, "visible":true, "points":[-239.5,-20,-229.5,-20,-220,-20,-220,-70,-210.5,-70,-200.5,-70]},
    {"from":-2, "to":-4, "visible":true, "points":[-239.5,-20,-229.5,-20,-220,-20,-220,30,-210.5,30,-200.5,30]},
    {"from":-4, "to":-8, "points":[-119.5,30,-109.5,30,-100,30,-100,30,-90.5,30,-80.5,30]},
    {"from":-3, "to":-5, "points":[-119.5,-70,-109.5,-70,-100,-70,-100,-70,-90.5,-70,-80.5,-70]},
    {"from":-5, "to":-10, "points":[0.5,-70,10.5,-70,30.25,-70,30.25,-20,50,-20,60,-20]},
    {"from":-8, "to":-10, "points":[0.5,30,10.5,30,30.25,30,30.25,-20,50,-20,60,-20]}
  ]}

class View extends Component {
  constructor(props) {
    super(props);
    this.state = {
      model: null,
      diagram: null,
      width: 0,
      height: 0
    };
  }

  renderCanvas () {
    const diagram = $(go.Diagram, this.refs[this.props.id], {
      allowDrop: true,
      initialAutoScale: go.Diagram.Uniform,
      initialContentAlignment: go.Spot.Center,
      "undoManager.isEnabled": true,
      "linkingTool.direction": go.LinkingTool.ForwardsOnly,
      "LinkDrawn": showLinkLabel,
      "LinkRelinked": showLinkLabel,
      layout: $(go.LayeredDigraphLayout,
        { isInitial: false, isOngoing: false, layerSpacing: 10 }),
    /*
      grid: $(go.Panel, "Grid",
        { gridCellSize: new go.Size(10, 10) },
        $(go.Shape, "LineH", { stroke: "lightgray", strokeWidth: 0.5 }),
        $(go.Shape, "LineH", { stroke: "gray", strokeWidth: 0.5, interval: 10 }),
        $(go.Shape, "LineV", { stroke: "lightgray", strokeWidth: 0.5 }),
        $(go.Shape, "LineV", { stroke: "gray", strokeWidth: 0.5, interval: 10 })
      ),

      "draggingTool.isGridSnapEnabled": true
      */
    });

    const adornment =
      $(go.Adornment, "Spot",
        $(go.Panel, "Auto",
          $(go.Shape, { fill: null, stroke: "dodgerblue", strokeWidth: 4 }),
          $(go.Placeholder)),
        // the button to create a "next" node, at the top-right corner
        $("Button",
          { alignment: go.Spot.TopRight,
            click: deleteNodeLink },  // this function is defined below
          new go.Binding("visible", "", function(a) { return !a.diagram.isReadOnly; }).ofObject(),
          $(go.Shape, "XLine", { desiredSize: new go.Size(6, 6) })
        )
      );

    diagram.nodeTemplateMap.add("",
      $(go.Node, "Spot",
        { selectionAdornmentTemplate: adornment },
        nodeStyle(),

        $(go.Panel, "Auto",
          $(go.Shape,
            "Circle",
            { fill: "rgb(255,255,255)", width: 70, height: 70, stroke: 'rgb(0,0,0)' },
            new go.Binding("figure", "figure")),

          $(go.TextBlock,
            "Default Text",  // the initial value for TextBlock.text
            { margin: 12, stroke: "#000", font: "11px Helvetica, Arial, sans-serif" },
            new go.Binding("text", "name"))
        ),
        makePort("L", go.Spot.Left, true, true),
        makePort("R", go.Spot.Right, true, true)
      ))

    diagram.nodeTemplateMap.add("Start",
      $(go.Node, "Spot", nodeStyle(),
        $(go.Panel, "Auto",
          $(go.Shape, "Circle",
            { minSize: new go.Size(40, 40), fill: "rgb(255,255,255)", stroke: 'rgb(0,0,0)' }),
          $(go.TextBlock, "Start",
            { font: "bold 11px Helvetica, Arial, sans-serif", stroke: 'rgb(0,0,0)' })
        ),
        makePort("R", go.Spot.Right, true, false),
      ))

    diagram.nodeTemplateMap.add("End",
      $(go.Node, "Spot", nodeStyle(),
        $(go.Panel, "Auto",
          $(go.Shape, "Circle",
            { minSize: new go.Size(40, 40), fill: "rgb(255,255,255)", stroke: 'rgb(0,0,0)' }),
          $(go.TextBlock, "End",
            { font: "bold 11px Helvetica, Arial, sans-serif", stroke: 'rgb(0,0,0)' })
        ),
        makePort("L", go.Spot.Left, false, true),
      ))


    diagram.linkTemplate =
      $(go.Link,  // the whole link panel
        new go.Binding("points").makeTwoWay(),
        { curve: go.Link.Bezier, toShortLength: 15 },
        new go.Binding("curviness", "curviness"),
        $(go.Shape,  // the link shape
          { stroke: "#2F4F4F", strokeWidth: 2.5 }),
        $(go.Shape,  // the arrowhead
          { toArrow: "kite", fill: "#2F4F4F", stroke: null, scale: 2 })
      )
      /*
      $(go.Link,
        {
          routing: go.Link.AvoidsNodes,
          curve: go.Link.JumpOver,
          //corner: 5,
          toShortLength: 4,
          relinkableFrom: true,
          relinkableTo: true,
          reshapable: true,
          resegmentable: true,
          mouseEnter: (e, link) => { link.findObject("HIGHLIGHT").stroke = "rgba(30,144,255,0.2)" },
          mouseLeave: (e, link) => { link.findObject("HIGHLIGHT").stroke = "transparent"; }
        },
        new go.Binding("points").makeTwoWay(),

        // Highlight Link shape
        $(go.Shape,
          { isPanelMain: true, strokeWidth: 8, stroke: "transparent", name: "HIGHLIGHT" }),
        // Link shape
        $(go.Shape,
          { isPanelMain: true, stroke: "gray", strokeWidth: 2 }),
        // Link Arrow
        $(go.Shape,
          { toArrow: "standard", stroke: null, fill: "gray"}),
        // Link Label
        $(go.Panel, "Auto",
          { visible: false, name: "LABEL", segmentIndex: 2, segmentFraction: 0.5},
          new go.Binding("visible", "visible").makeTwoWay(),
          $(go.Shape, "RoundedRectangle",
            { fill: "#F8F8F8", stroke: null }),
          $(go.TextBlock, "Label",  // the label
            {
              textAlign: "center",
              font: "10pt helvetica, arial, sans-serif",
              stroke: "#333333",
              editable: true
            },
            new go.Binding("text").makeTwoWay())
        ))
        */


    function showLinkLabel(e) {
      let label = e.subject.findObject("LABEL");
      if (label !== null) label.visible = (e.subject.fromNode.data.figure === "Diamond");
    }

    diagram.addDiagramListener("ObjectSingleClicked",
      function(e) {
        var part = e.subject.part;
        if (!(part instanceof go.Link)) console.log("Clicked on " + part.data.name);
      });


    function nodeStyle() {
      return [
        new go.Binding("location", "loc", go.Point.parse).makeTwoWay(go.Point.stringify),
        {
          locationSpot: go.Spot.Center,
          mouseEnter: function (e, obj) { showPorts(obj.part, true); },
          mouseLeave: function (e, obj) { showPorts(obj.part, false); }
        }
      ];
    }

    function makePort(name, spot, output, input) {
      return $(go.Shape, "Circle",
        {
          fill: "transparent",
          stroke: null,  // this is changed to "white" in the showPorts function
          desiredSize: new go.Size(12, 12),
          alignment: spot, alignmentFocus: spot,  // align the port on the main Shape
          portId: name,  // declare this object to be a "port"
          fromSpot: spot, toSpot: spot,  // declare where links may connect at this port
          fromLinkable: output, toLinkable: input,  // declare whether the user may draw links to/from here
          cursor: "pointer"  // show a different cursor to indicate potential link point
        });
    }

    function showPorts(node, show) {
      node.ports.each(function(port) {
        if (port.portId !== "") {  // don't change the default port, which is the big shape
          port.fill = show ? "rgba(0,0,0,.3)" : null;
        }
      });
    }

    function deleteNodeLink(){
      diagram.commandHandler.deleteSelection();
    }

    diagram.model = go.Model.fromJson({
      "class": myModel.class,
      "nodeDataArray": myModel.nodeDataArray.map(obj => ({...obj})),
      "linkDataArray": myModel.linkDataArray.map(obj => ({...obj}))
    });


    this.setState({
      ...this.state,
      diagram
    });
  }

  componentDidMount () {
    this.renderCanvas ();
  }

  componentDidUpdate (prevProps, nextProps) {
//    this.state.diagram.requestUpdate();
  }

  componentWillUnmount (){
    this.setState({
      model: null,
      diagram: null,
      width: 0,
      height: 0
    });
  }


  render() {
    let { width, height, id } = this.props
    return (
      <div
        className="goJsDiv"
        ref={ id }
        style={{ width: width, height }}
      />
    )
  }
}

export default View;


